const updateLog = {
    '1.0': {
        lastPitch: null,
        currentPitch: '1.0',
        content: [
            '角色卡的创建方式从本地工作簿升级成了网页形式。',
            '信仰调整：种族信仰的【中立】选项改成了【种族平等】。',
            '信仰调整：【阶级信仰】改成了【权力信仰】，包括集权和平等。',
            '信仰调整：【认知信仰】改成了【处世信仰】，包括玄学和科学。',
            '技能系统调整：技能现在将会分类，所有通用类技能均为100%点数消耗，且不再消耗任何领悟力。',
            '技能调整：闪避、说服、估价的技能初始值从【50】上调到【100】。',
            '技能调整：格挡反击的技能百分比由【10%力量+30%体质+30%灵巧+30%通用】调整为【30%力量+20%体质+20%灵巧+30%通用】。',
            '技能调整：装备保养的技能百分比由【80%智慧+20%通用】调整为【40%智慧+60%通用】。',
            '技能调整：援助格挡的技能百分比由【20%力量+100%体质+30%通用】调整为【80%力量+40%体质+30%通用】。',
            '技能调整：药理医疗的技能等阶由【三阶】下调为【二阶】，技能百分比由【130%智慧+70%通用】调整为【80%智慧+70%通用】。',
            '次级属性调整：士气与暴击取消最大值限制，倍率放大至10倍且判定通过的规则由【不大于rd100】改为【不大于rd1000】。',
            '次级属性调整：先攻值的计算公式由【灵巧】调整为【灵巧^0.9*意志^0.1】。',
        ],
    },
    '1.1': {
        lastPitch: '1.0',
        currentPitch: '1.1',
        content: [],
    },
}

const getAllUpdates = (pitch) => {
    const updates = []
    const currentLog = updateLog[pitch]
    updates.push(currentLog)
    if (!currentLog.lastPitch) { return updates }
    const previousUpdates = getAllUpdates(currentLog.lastPitch)
    previousUpdates.forEach(update => {
        updates.push(update)
    })
    return updates
}

export { getAllUpdates }